
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'

import api from '../api/api'
import auth from '../api/auth'
import env from '../config/env'
export default {
    data() {
        return {
            // 
            verifyCode: '',
            img_url: null,
            phone: '',
            // 
            inputVerifyCode: '',
            showLoading: false
        }
    },
    mounted() {
        this.getVerifyCode()
    },
    methods: {
        // 
        getVerifyCode() {
            let vm = this
            // 
            api.get('getWeChatVerifyCode').then((res) => {
                if(res.status===1){
                    vm.verifyCode = res.data.verifyCode
                    vm.img_url = env.serverUrl + res.data.img_url
                }else{
                    vm.$vux.alert.show({
                        title: env.alertInfo,
                        content: res.message,
                        buttonText: '',
                        dialogTransition:'',
                        maskTransition:''
                    })
                }
            }).catch(error => {
                vm.$vux.alert.show({
                    title: env.alertInfo,
                    content: env.errorInfo,
                    buttonText: '',
                    dialogTransition:'',
                    maskTransition:''
                })
            })
        },
        // 
        login() {
            if (!this.phone) {
                this.$vux.alert.show({
                    title: env.alertInfo,
                    content: '',
                    buttonText: '',
                    dialogTransition:'',
                    maskTransition:''
                })
                return
            }
            if (!this.inputVerifyCode) {
                this.$vux.alert.show({
                    title: env.alertInfo,
                    content: '',
                    buttonText: '',
                    dialogTransition:'',
                    maskTransition:''
                })
                return
            }
            if (this.inputVerifyCode.toLowerCase() != this.verifyCode.toLowerCase()) {
                this.$vux.alert.show({
                    title: env.alertInfo,
                    content: '',
                    buttonText: '',
                    dialogTransition:'',
                    maskTransition:''
                })
                return
            }
            // 
            auth.login(this.phone, () => {
                    // 
                    this.$router.replace(this.$route.query.redirect || '/')
                }, 
                () => {
                    // 
                    this.$vux.alert.show({
                        title: '',
                        content: '',
                        buttonText: '',
                        dialogTransition:'',
                        maskTransition:''
                    })
                    // 
                    this.getVerifyCode()
                })
        }
    },
    components: {
        XHeader, 
        XInput, 
        XButton, 
        Group
    }
}



